import React from "react";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

import IntlMessages from "../../../helpers/IntlMessages";

const EditProfile = ({ showEditUI, setShowEditUI }) => {
  return (
    <Modal
      isOpen={showEditUI}
      toggle={() => setShowEditUI(!showEditUI)}
      size="lg"
    >
      <ModalHeader>
        <IntlMessages id="user.edit-profile.title" />
      </ModalHeader>
      <ModalBody>
        <IntlMessages id="user.edit-profile.desc" />
      </ModalBody>
      <ModalFooter>
        <Button color="primary" onClick={() => setShowEditUI(false)}>
          <IntlMessages id="user.edit-profile.save" />
        </Button>
        <Button color="danger" onClick={() => setShowEditUI(false)}>
          <IntlMessages id="user.edit-profile.cancel" />
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default EditProfile;
