import React, { useState } from "react";
import {
  Row,
  Card,
  CardBody,
  Button,
  Badge,
  CardTitle,
  CardSubtitle,
  CardText,
  CardImg,
} from "reactstrap";
import { NavLink, useHistory } from "react-router-dom";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import { Colxx } from "../../../components/common/CustomBootstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import SingleLightbox from "../../../components/pages/SingleLightbox";
import RecentPost from "../../../components/common/RecentPost";
import recentPostsData from "../../../data/recentposts";
import productData from "../../../data/products";
import EditProfile from "./edit-profile";
import { getCurrentUser } from "../../../helpers/Utils";
import { adminRoot } from "../../../constants/defaultValues";

const products = productData.slice(0, 15);

const ProfileView = ({ match }) => {
  const [showEditUI, setShowEditUI] = useState(false);
  const currentUser = getCurrentUser();
  const history = useHistory();

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <h1>{currentUser.title}</h1>

          <Breadcrumb match={match} />

          <Row>
            <Colxx xxs="12" lg="5" className="mb-4 col-left">
              <Card className="mb-4">
                <div className="position-absolute card-top-buttons">
                  <Button
                    outline
                    color="white"
                    onClick={() => {
                      history.push(`${adminRoot}/profile/about`);
                    }}
                    className="icon-button"
                  >
                    <i className="simple-icon-pencil" />
                  </Button>
                </div>
                <SingleLightbox
                  thumb="/assets/img/profiles/10.jpg"
                  large="/assets/img/profiles/10.jpg"
                  className="card-img-top"
                />

                <CardBody>
                  <p className="text-muted text-small mb-2">
                    <IntlMessages id="menu.about" />
                  </p>
                  <p className="mb-3">
                    <IntlMessages id="user.bio" />
                    <br />
                    I’m a web developer. I spend my whole day, practically every
                    day, experimenting with HTML, CSS, and JavaScript; dabbling
                    with Python and Ruby; and inhaling a wide variety of
                    potentially useless information through a few hundred RSS
                    feeds. I build websites that delight and inform. I do it
                    well.
                  </p>
                  <p className="text-muted text-small mb-2">
                    <IntlMessages id="pages.location" />
                  </p>
                  <p className="mb-3">Nairobi, Kenya</p>
                  <p className="text-muted text-small mb-2">
                    <IntlMessages id="pages.responsibilities" />
                  </p>
                  <p className="mb-3">
                    <Badge color="outline-secondary" className="mb-1 mr-1" pill>
                      FRONTEND
                    </Badge>
                    <Badge color="outline-secondary" className="mb-1 mr-1" pill>
                      JAVASCRIPT
                    </Badge>
                    <Badge color="outline-secondary" className="mb-1 mr-1" pill>
                      SECURITY
                    </Badge>
                    <Badge color="outline-secondary" className="mb-1 mr-1" pill>
                      DESIGN
                    </Badge>
                  </p>
                  <p className="text-muted text-small mb-2">
                    <IntlMessages id="menu.contact" />
                  </p>
                  <div className="social-icons">
                    <ul className="list-unstyled list-inline">
                      <li className="list-inline-item">
                        <NavLink to="#" target="_blank" location={{}}>
                          <i className="simple-icon-social-facebook" />
                        </NavLink>
                      </li>
                      <li className="list-inline-item">
                        <NavLink to="#" location={{}}>
                          <i className="simple-icon-social-twitter" />
                        </NavLink>
                      </li>
                      <li className="list-inline-item">
                        <NavLink to="#" location={{}}>
                          <i className="simple-icon-social-instagram" />
                        </NavLink>
                      </li>
                    </ul>
                  </div>
                </CardBody>
              </Card>

              <Card className="mb-4">
                <CardBody>
                  <CardTitle>
                    <IntlMessages id="pages.recent-posts" />
                  </CardTitle>
                  <div className="remove-last-border remove-last-margin remove-last-padding">
                    {recentPostsData.map((itemData) => {
                      return (
                        <RecentPost
                          data={itemData}
                          key={`recent_${itemData.key}`}
                        />
                      );
                    })}
                  </div>
                </CardBody>
              </Card>
            </Colxx>

            <Colxx xxs="12" lg="7" className="mb-4 col-right">
              <Row>
                {products.map((product) => {
                  return (
                    <Colxx
                      xxs="12"
                      lg="6"
                      xl="4"
                      className="mb-4"
                      key={`product_${product.id}`}
                    >
                      <Card>
                        <div className="position-relative">
                          <NavLink
                            to="#"
                            location={{}}
                            className="w-40 w-sm-100"
                          >
                            <CardImg
                              top
                              alt={product.title}
                              src={product.img}
                            />
                          </NavLink>
                        </div>
                        <CardBody>
                          <NavLink
                            to="#"
                            location={{}}
                            className="w-40 w-sm-100"
                          >
                            <CardSubtitle>{product.title}</CardSubtitle>
                          </NavLink>
                          <CardText className="text-muted text-small mb-0 font-weight-light">
                            {product.createDate}
                          </CardText>
                        </CardBody>
                      </Card>
                    </Colxx>
                  );
                })}
              </Row>
            </Colxx>
          </Row>
        </Colxx>
      </Row>

      <EditProfile showEditUI={showEditUI} setShowEditUI={setShowEditUI} />
    </>
  );
};
export default ProfileView;
